.colors{
  color: #2c0735;
  color: #4E148C;
  color: #613DC1;
  color: #858AE3;
  color: #97DFFC;
}

@keyframes purple-breath {
  from {background-color: #2c0735;}
  to {background-color: #4E148C;}
}

.App {
  text-align: center;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: rgb(57, 19, 68);
  min-height: 100vh;
  max-width: 100%;
  display: grid;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.Main-div {
  height: 100%;
}

.Title-div {
  background-color: #2c0735;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  animation-name: purple-breath;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.Title-div div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Title-div div a {
  margin: 0 10px 0 10px;
  color: white;
  text-decoration: none;
}

.Title-div div a:visited {
  color: white;
}

.Title-div div a:hover {
  color: #97DFFC;
}

.Title-div div a:active {
  border: none;
}

.Blurb-div {
  background-color: #858AE3;
  width: 100vw;
  height: 50vh;
}

.Skills-div {
  background-color: #97DFFC;
  width: 100vw;
  height: 100vh;
}